import React from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { FaHome, FaExclamationTriangle, FaRocket, FaCode } from 'react-icons/fa';
import styled, { keyframes } from 'styled-components';

// Animations
const float = keyframes`
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  50% { transform: translateY(-20px) rotate(5deg); }
`;

const pulse = keyframes`
  0%, 100% { transform: scale(1); opacity: 0.7; }
  50% { transform: scale(1.1); opacity: 1; }
`;

const glow = keyframes`
  0%, 100% { box-shadow: 0 0 20px rgba(220, 20, 60, 0.3); }
  50% { box-shadow: 0 0 40px rgba(255, 105, 180, 0.6); }
`;

// Styled Components
const Container = styled.section`
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 5;
  padding: 100px 0;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.95), rgba(26, 26, 26, 0.9));
  overflow: hidden;
`;

const BackgroundAnimation = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  
  &::before {
    content: '';
    position: absolute;
    top: 20%;
    left: 10%;
    width: 200px;
    height: 200px;
    background: radial-gradient(circle, rgba(220, 20, 60, 0.1), transparent);
    border-radius: 50%;
    animation: ${float} 8s ease-in-out infinite;
  }
  
  &::after {
    content: '';
    position: absolute;
    bottom: 20%;
    right: 10%;
    width: 150px;
    height: 150px;
    background: radial-gradient(circle, rgba(255, 105, 180, 0.1), transparent);
    border-radius: 50%;
    animation: ${float} 6s ease-in-out infinite reverse;
  }
`;

const ErrorNumber = styled(motion.div)`
  font-size: 8rem;
  font-weight: 700;
  background: linear-gradient(135deg, #DC143C, #FF69B4);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 20px;
  text-shadow: 0 0 30px rgba(220, 20, 60, 0.5);
  animation: ${pulse} 2s ease-in-out infinite;
`;

const IconContainer = styled(motion.div)`
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 30px;
`;

const Icon = styled.div`
  font-size: 3rem;
  color: ${props => props.color};
  animation: ${float} 3s ease-in-out infinite;
  animation-delay: ${props => props.delay || '0s'};
`;

const Title = styled(motion.h1)`
  font-size: clamp(2rem, 5vw, 3rem);
  font-weight: 700;
  margin-bottom: 20px;
  color: #ffffff;
  line-height: 1.2;
  text-align: center;
`;

const Description = styled(motion.p)`
  font-size: clamp(1.1rem, 2.5vw, 1.3rem);
  color: #cccccc;
  margin-bottom: 40px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
  text-align: center;
`;

const HomeButton = styled(Link)`
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 15px 30px;
  background: linear-gradient(135deg, #DC143C, #FF69B4);
  color: white;
  text-decoration: none;
  border-radius: 30px;
  font-size: 1.1rem;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 10px 20px rgba(220, 20, 60, 0.3);
  animation: ${glow} 3s ease-in-out infinite;
  
  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 15px 30px rgba(220, 20, 60, 0.5);
  }
`;

const ErrorInfo = styled(motion.div)`
  margin-top: 60px;
  padding: 20px;
  background: linear-gradient(135deg, rgba(220, 20, 60, 0.1), rgba(255, 105, 180, 0.1));
  border-radius: 15px;
  border: 1px solid rgba(220, 20, 60, 0.3);
  backdrop-filter: blur(10px);
`;

const NotFound = () => {
  return (
    <Container>
      <BackgroundAnimation />
      <div style={{
        maxWidth: '800px',
        margin: '0 auto',
        padding: '0 20px',
        textAlign: 'center',
        position: 'relative',
        zIndex: 1
      }}>
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
        >
          <ErrorNumber>404</ErrorNumber>
        </motion.div>

        <IconContainer
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.2 }}
        >
          <Icon color="#DC143C" delay="0s">
            <FaExclamationTriangle />
          </Icon>
          <Icon color="#FF69B4" delay="0.5s">
            <FaRocket />
          </Icon>
          <Icon color="#DC143C" delay="1s">
            <FaCode />
          </Icon>
        </IconContainer>

        <Title
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.4 }}
        >
          Oops! Page Not Found
        </Title>

        <Description
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.6 }}
        >
          The page you're looking for doesn't exist. It might have been moved, deleted, or you entered the wrong URL.
        </Description>

        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.8 }}
        >
          <HomeButton to="/">
            <FaHome style={{ fontSize: '1.2rem' }} />
            Back to Home
          </HomeButton>
        </motion.div>

        <ErrorInfo
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1, delay: 1 }}
        >
          <p style={{
            fontSize: '0.9rem',
            color: '#cccccc',
            margin: 0
          }}>
            Error Code: 404 | Status: Not Found
          </p>
        </ErrorInfo>
      </div>
    </Container>
  );
};

export default NotFound;
